.logo {
  background-image: url("../../images/header/__logo/logo-white.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: transparent;
  width: 142px;
  height: 33px;
  margin: 0;
}

@media screen and (max-width: 768px) {
  .logo {
    width: 103.74px;
    height: 24.4px;
  }
}
