.popup {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, .5);
  padding: 0 19px;
  opacity: 0;
  visibility: hidden;
  z-index: 0;
  transition: all 0.3s ease-out;
}

.popup:hover {
  cursor: pointer;
}
