.header {
  max-width: 880px;
  margin: 0 auto;
  padding-bottom: 41px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 45px 0 41px;
}

@media screen and (max-width: 927px) {
  .header {
    padding: 45px 27px 41px;
  }
}

@media screen and (max-width: 768px) {
  .header {
    padding: 28px 27px 31.6px;
  }
}
